
import { defineComponent } from 'vue';
import FormularioContato from "@/components/FormContact.vue";

export default defineComponent({
  name: "Contato",
  components: { FormularioContato },
  data(){
    return{
      modal: false as boolean,
      nome: '' as String,
    }
  },
  methods:{
    guardarNome(nome:String){
      this.nome = nome;
    },
    mostrarModal(){
      this.modal = true;
      
    },
    fecharModal(){
      this.modal = false;
    }
  }
});
